.Home{
    background: grey;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.main{
    margin-bottom: 20%;
}
.stream{
    position: relative;
}
.hidden{
    position: absolute;
    top: 0;
    background: grey;
    width: 100%;
    height: 20%;
}
.hidden-2{
    position: absolute;
    bottom: 1%;
    right: 0;
    background: grey;
    width: 76%;
    height: 25.5%;
    border-top: 1px solid white;
    border-left: 1px solid white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 35px;
}
.hidden-3{
    position: absolute;
    bottom: 11%;
    left: 0;
    background: white;
    width: 23.85%;
    height: 2%;
}
.message-container{
    margin-top: 5%;
    width: 642px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.enter-input{
    width:440px;
    height: 40px;
}
.confirm-input{
    width: 40px;
    height: 46px;
    font-size: 18px;
    text-align: center;
    cursor: pointer;
}